html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  padding-top: 4rem;
  margin: 0;
}

/* CONTAINERS */
.container {
  margin: 20px auto;
  max-width: 1000px;
  padding: 0 20px;
}

.container-sm {
  margin: 20px auto;
  width: 75%;
  max-width: 640px;
  padding: 0 20px;
}

/* HEADER */
.header {
  padding: 20px;
  text-align: center;
}

/* HERO */
/* CHANGE background-image & background-color ACCORDINGLY*/
.hero-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(https://i.imgur.com/tDTb9SG.png);
  background-color: #fff523;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 100%;
  position: relative;
}

.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}
